import { makeAutoObservable } from "mobx";

class CounterStore {
  zIndex = 5; // 

  constructor() {
    makeAutoObservable(this); // 
  }

  /**
   * 
   */
  addZIndex = () => {
    this.zIndex++;
  };

  /**
   * 
   */
  getZIndex = (): string => String(this.zIndex);
}

export default new CounterStore();
